import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import ScrollIntoView from '../../components/util/scrollIntoView';

@observer
class Teams extends Component {
    toggleTeam = (team) => {
		const { callback, unsetTeamIfSelected = true } = this.props;
        const { dogStore, workoutStore } = this.props.stores;
		const { currentTeam } = dogStore;
		if (unsetTeamIfSelected && team === currentTeam) {
			dogStore.setCurrentTeam(undefined);
			workoutStore.setCurrentTeam(undefined);
		} else {
			dogStore.setCurrentTeam(team);
			workoutStore.setCurrentTeam(team);
		}
		if (util.isDefined(callback)) {
			callback();
		}
	}

    showAllTeams = () => {
		const { callback } = this.props;
        const { dogStore, workoutStore } = this.props.stores;
		dogStore.setCurrentTeam(undefined);
		workoutStore.setCurrentTeam(undefined);
		if (util.isDefined(callback)) {
			callback();
		}
	}

	// componentDidMount() {
	// 	if (this.container) {
	// 		this.container.scrollIntoView({ behavior: 'smooth', block: 'end', inline: 'center' });
	// 	}
	// }

    render() {
        const { userStore, dogStore } = this.props.stores;
        const { teams } = userStore;
		const { currentTeam } = dogStore;
		const { highlight, showAll = true } = this.props;
        return (
			<div class='row mt-2'>
				<div class='w-100 overflow-hidden'>
					<div
						class='d-flex flex-row flex-nowrap pb-2 px-1'
						style='overflow: auto; scroll-snap-type: x mandatory;'
					>
						{/* <xmp><br /><br /><br /><br />{JSON.stringify(teams, null, 4)}</xmp> */}
						{teams && teams.map((team, idx) => (
							<div class='col mb-0 px-0 clearfix' key={team.id}>
								<button
									class={`btn btn-sm btn-block 
										rounded-none ${idx === 0 ? 'rounded-pill-left' : (idx + 1 === teams.length ? 'rounded-pill-right': '')}
										${currentTeam === team.id ? 'btn-success' : 'btn-outline-success'}
										text-nowrap clearfix`}
									style='text-overflow: ellipsis; overflow: hidden;'
									onClick={() => this.toggleTeam(team.id)}
								>
									<i class='fas fa-users' /> {team.name}

									{highlight && highlight.indexOf(team.id) > -1 && <>
										{currentTeam === team.id ? <>
											<i class='fas fa-check float-right' />
										</> : <>
											<div class='spinner-grow text-warning float-right' role='status'>
												<span class='sr-only'>Loading...</span>
											</div>
											<i class='far fa-hand-point-left float-right' style='font-size: 2.0em;' />   
										</>}
									</>}
								
								</button>
								{/* {currentTeam === team.id && <ScrollIntoView horizontal={'center'} vertical={'end'} />} */}

							</div>
						))}
					</div>
					{showAll && <div class='row'>
						<div class='col-12 mb-1 px-1 d-flex justify-content-center'>
							<button
								class={`btn btn-sm rounded-lg btn-link text-nowrap clearfix`}
								style='text-overflow: ellipsis; overflow: hidden;'
								onClick={this.showAllTeams}
							>
								<i class='fas fa-users' /> <Text id='teams.show-all'>Show all</Text>
							</button>
						</div>
					</div>}
				</div>
			</div>
        );
    }
}

export default Teams;
